@import '../../styles/config/variables.scss';

  .footer_container{
  @media screen and (min-width: 1261px) {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 92%;
    padding-top: 25px;
    margin-bottom: 10px;
  }
  @media screen and (min-width: 970px) and (max-width:1260px) {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 95%;
    padding-top: 15px;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    max-width: 90%;
    padding-top: 10px;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
  @media screen and (min-width: 320px) and (max-width:599px) {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    max-width: 85%;
    padding-top: 10px;
    margin-bottom: 10px;
  }
  }

.footer_follow_us{
  @media screen and (min-width:1201px){

  }
  @media screen and (min-width: 970px) and (max-width:1200px) {
    max-width: 40%;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    max-width: 45%;
  }
}
.footer_block_logo_title{
  display: flex;
  @media screen and (min-width: 600px) and (max-width:969px) {
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    display: flex;
    justify-content: center;
    }
}
.footer_block_logo{
  width: 35px;
  height: 35px;
  @media screen and (min-width: 970px) and (max-width:1260px){
    width: 35px;
    height: 35px;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    width: 25px;
    height: 25px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    width: 20px;
    height: 20px;
    }
}
.footer_block_text{
  font-family: $body-font;
  color: $title-color;
  padding-left: 25px;
  font-size: 30px;
  font-weight: 700;
  @media screen and (min-width: 970px) and (max-width:1260px){
    font-weight: 600;
    font-size: 25px;
    padding-left: 15px;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    font-size: 20px;
    padding-left: 10px;
    font-weight: 600;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
  padding-left: 15px;
  font-size: 15px;
  font-weight: 600;
  }
}
.footer_block_description{
  font-family: $body-font;
  padding-top: 20px;
  max-width: 427px;
  font-size: 16px;
  line-height: 24px;
  color: $text-color-light;
  @media screen and (min-width: 970px) and (max-width:1260px){
    padding-top: 10px;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    padding-top: 10px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    padding-top: 5px;
    font-size: 14px;
    text-align: center;
  }
}

.footer_quick_links{
  @media screen and (min-width: 1201px) {
  display: flex;
  flex-direction: column;
  }
  @media screen and (min-width: 970px) and (max-width:1200px) {
    max-width: 25%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    max-width: 20%;
    display: flex;
    flex-direction: column;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    display: flex;
    flex-direction: column;
    max-width: 45%;
  }
}
.footer_quick_links_title{
  font-family: $body-font;
  color: $title-color;
  margin-bottom: 5px;
  font-weight: 600;
  @media screen and (min-width: 970px) and (max-width:1260px){
    margin-bottom: 4px;
    font-family: $body-font;
    color: $title-color;
    font-weight: 500;
  }
  @media screen and (min-width: 600px) and (max-width:969px){
    margin-bottom: 4px;
    font-family: $body-font;
    color: $title-color;
    font-weight: 500;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    margin-top: 5px;
    
  }
}

.footer_customer_area{
  @media screen and (min-width: 1201px) {
  display: flex;
  flex-direction: column;
  }
  @media screen and (min-width: 970px) and (max-width:1200px) {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 25%;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    max-width: 20%;
    display: flex;
    flex-direction: column;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    display: flex;
    flex-direction: column;
    max-width: 45%;
  }
}
.footer_customer_area_tittle{
  font-family: $body-font;
  color: $title-color;
  margin-bottom: 7px;
  font-weight: 600;
  @media screen and (min-width: 968px) and (max-width:1260px){
    margin-bottom: 4px;
    font-weight: 500;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    font-weight: 500;
    margin-bottom: 2px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    margin-top: 5px;
    
  }
}
.footer_quick_links_link{
  font-family: $body-font;
  color: $text-color-light;
  margin-top: 5px;
  &:hover {
    color: black;
    transition: 0.25s;
}
  @media screen and (min-width: 968px) and (max-width:1260px){
    margin-top: 5px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    margin-top: 5px;
  }
}

.footer_subscribe{
  @media screen and (min-width: 1260px) {
  width: 500px;
  }
  @media screen and (min-width: 970px) and (max-width:1260px) {
    width: 370px;
  }
  @media screen and (min-width: 600px) and (max-width:969px) {
    width: 280px;
    margin-left: 5px;
    margin-right: 2%;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    margin-bottom: 10px;
    margin-left: 10%;
    width: 80%;
    margin-top: 7px;
  }
}
.footer_input_subscribe{
  font-family: $body-font;
  width: 65%;
  min-height: 55px;
  border-radius: 8px;
  border: none;
  background: #F3F3F329;
  color: #E2E2E2;
  padding-left: 10px;
  @media screen and (min-width: 970px) and (max-width:1260px){
    max-width: 70%;
    min-height: 50px;
    margin: 0;
  }
  @media screen and (min-width: 600px) and (max-width:969px){
    max-width: 65%;
    min-height: 40px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    min-height: 40px;
    margin: 0;
  }
}
.footer_input_subscribe errors{
  position: absolute;
}
.footer_button_sumbmit {
  margin-left: 15px;
  color: #8D28AD;
  padding: 15px 22px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  background: #FFFFFF;;
  border-radius: 8px;
  &:hover{
    background-color: #E5E5E5;
    color: 8D28AD;
    transition: 0.25s;
  }
  @media screen and (min-width: 970px) and (max-width:1260px){
    font-size: 13px;
    min-height: 29px;
    padding: 12px 10px;
  }
  @media screen and (min-width: 600px) and (max-width:969px){
    padding: 9px 9px;
    font-size: 12px;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    max-width: 25%;
    padding: 8px 8px;
    font-size: 10px;
    margin-right: 2%;
    &:hover{
      background-color: #E5E5E5;
      color: #8D28AD;
    }
  }
}


.footer_subscribe_block{
  display: flex;
  justify-content: space-between;
  background: $first-color;
  min-height: 150px;
  width: 100%;
  align-items: center;
  @media screen and (min-width: 970px) and (max-width:1260px){
    min-height: 140px;
    width: 100%;
  }
  @media screen and (min-width: 600px) and (max-width:969px){
    min-height: 110px;
    width: 100%;
  }
  @media screen and (min-width: 320px) and (max-width:599px){
    min-height: 90px;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}
.footer_subscribe_title{
    margin-left: 3%;
    font-weight: 600;
    font-size: 28px;
    line-height: 55px;
    color: $text-color;
    max-width: 50%;
  
  @media screen and (min-width: 970px) and (max-width:1260px){
    font-weight: 550;
    font-size: 30px;
    line-height: 50px;
    color: $text-color;
    max-width: 480px;

  }
  @media screen and (min-width: 600px) and (max-width:969px){
    font-weight: 500;
    font-size: 20px;
    line-height: 35px;
    color: #E5E5E5;
    max-width: 48%;

  }
  @media screen and (min-width: 320px) and (max-width:599px){
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #E5E5E5;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    max-width: 90%;
  }
}
