@import '../../styles/config/variables.scss';


.categoryPage{
    padding:2vw;
}
.categoryPage__container{
    max-width:1024px;
    margin: 10vh auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 180px));
    gap:1rem;

        @include breakpoint(md-only){
            grid-template-columns: repeat(auto-fit, minmax(200px, auto));
        }
        @include breakpoint(sm-only){
            grid-template-columns: repeat(auto-fit, minmax(150px, auto));
        }
}


.categoryPage__loader{
    margin-top: 20vh;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

}

.categoryPage__textarea{
    position: relative;
    max-width:1024px;
    margin: 10vh auto;
    text-transform:capitalize;
    font-size:$h1-font-size;
    &::after {
        content: "";
        position: absolute;
        width: 20%;
        height: 3px;
        bottom: 0;
        left: 0;
        right: 0;
        border-radius: 10px;
        background: $first-color;

        @include breakpoint(sm-only){
            width: 40%;
        }
    }
}